body {
  padding: 50px;
  background-color:lightsteelblue;
  font-family: monospace;
  text-align: center;
  font-family: monospace;
}
#root {
  display: flex;
  justify-content: center;
}
h1 {
  letter-spacing: 2px;
  font-size: 2.1em;
}
h4 {
  letter-spacing: 2px;
  font-size: 1.1em;
  font-weight: normal;
}
.title {
  background-color: orange;
  padding: 30px;
}
.author {
  letter-spacing: 2px;
  position: fixed;
  bottom: 0;
}
.author i {
  color: black;
}
.author i:hover {
  color: pink;
}
.appMain .header input {
  padding: 10px;
  font-size: 16px;
  border: 2px solid #FFF;
  width: 180px;
  font-family: monospace;
  border-radius: 3px;
  letter-spacing: 1px;
  text-align: center;
}

.appMain .header button {
  padding: 10px;
  font-size: 16px;
  margin: 10px;
  margin-right: 0px;
  background-color: #0066FF;
  color: #FFF;
  border: 2px solid #0066FF;
  border-radius: 2px;
  letter-spacing: 1px;
}

.appMain .header button:hover {
  background-color: #003399;
  border: 2px solid #003399;
  cursor: pointer;
}

.appMain .theList {
  list-style: none;
  padding-left: 0;
  width: 270px;
} 

.appMain .theList li {
  color: #333;
  background-color: rgba(255,255,255,.5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  list-style: none;
}

ul.theList {
  padding: 0;
}

.appMain .theList li {
  color: #333;
  background-color: rgba(255,255,255,.5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  letter-spacing: 2px;
  transition: background-color .2s ease-out;
}
 
.appMain .theList li:hover {
  background-color: pink;
  cursor: pointer;
}
